.card{
    padding: 25px;
    background: var(--btn-color);
    position: relative;
    display: flex;
    flex-direction: column;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
}
.service-card__container > .card{
    opacity: 0;
}
.card-icon,.card-image{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    overflow: hidden;
    width: 62px;
    height: 62px;
    line-height: 62px;
    border-radius: 50%;
    background: var(--bg-item);
    color: var(--btn-color);
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
}
.card-image{
    width: 100px;
    height: 100px;
}
.card-image img{
    position: absolute;
    width: 100%;
    object-fit: cover;
}
.card-title{
    align-self: center;
    margin: 20px 0;
    font-weight: 400;
    font-size: 17px;
    color: var(--item-title-color);
}
.italic{
    font-style: italic;
}